body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:#D7DBDD;
}

img{
  width: 300px;
  height: 170px;
}

a{
  text-decoration: none;
  color: black;
  height: 2rem;
  width: 90%;
}

.cards{
  width: 100%;
  height: auto;
}

.card{
  margin: 1%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 23%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
  align-items: center;
  text-align: center;
}

.card:hover{
  box-shadow: 0px 30px 18px -8px  rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card img{
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

button{
  padding: 5px 8px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  margin-bottom: 1rem;
  cursor: pointer;
}



@media only screen and (max-width: 800px) {
  .card{
    margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 88%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: none;
  align-items: center;
  text-align: center;
  }
}

header{
  background-color: black;
  text-align: right;
  height: 3.3rem;
}
.header{
  color: red;
  margin-right: 25px;
  font-size: 2.8rem;
  cursor: pointer;
}
footer{
  text-align: center;
  margin-top: 1rem;
}